import React, { useRef, useState, useEffect } from 'react';
import styled from 'styled-components';
import { 
  WhatsappShareButton, 
  TwitterShareButton, 
  FacebookShareButton, 
  TelegramShareButton 
} from 'react-share';
import { CardTemplate } from '../types';

interface CardPreviewProps {
  recipientName: string;
  selectedCard: CardTemplate;
  onReset: () => void;
}

const PreviewContainer = styled.div`
  max-width: 600px;
  margin: 0 auto;
  direction: rtl;
`;

const CardContainer = styled.div`
  position: relative;
  margin-bottom: 30px;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
`;

const StyledImage = styled.img`
  width: 100%;
  display: block;
`;

const NameOverlay = styled.div`
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 28px;
  font-weight: bold;
  color: white;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  padding: 10px;
`;

const ShareContainer = styled.div`
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 25px;
`;

const ShareButton = styled.div`
  cursor: pointer;
  transition: transform 0.3s ease;
  
  &:hover {
    transform: scale(1.1);
  }
`;

const Button = styled.button`
  background-color: #4caf50;
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 15px;
  
  &:hover {
    background-color: #45a049;
  }
`;

// مكون جديد للكانفاس المخفي
const HiddenCanvas = styled.canvas`
  display: none;
`;

const CustomShareButton = styled.div`
  background-color: #4caf50;
  color: white;
  padding: 10px 15px;
  border-radius: 25px;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  max-width: 200px;
  margin: 0 auto 20px;
  
  &:hover {
    background-color: #45a049;
  }
`;

const ShareIcon = styled.span`
  margin-left: 8px;
  font-size: 18px;
`;

const CardPreview: React.FC<CardPreviewProps> = ({ recipientName, selectedCard, onReset }) => {
  const cardRef = useRef<HTMLDivElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [shareUrl, setShareUrl] = useState('');
  const [shareImage, setShareImage] = useState<string | null>(null);
  const [shareTitle, setShareTitle] = useState('');
  const [generatedImage, setGeneratedImage] = useState<string | null>(null);
  const [showShareButtons, setShowShareButtons] = useState(false);
  
  // إنشاء الصورة المدمجة مع النص
  useEffect(() => {
    const generateImage = async () => {
      const img = new Image();
      img.crossOrigin = "Anonymous"; // للسماح باستخدام الصور من مصادر خارجية
      
      img.onload = () => {
        const canvas = canvasRef.current;
        if (!canvas) return;
        
        // تحديد أبعاد الكانفاس لتطابق الصورة
        const aspectRatio = img.width / img.height;
        canvas.width = 800; // عرض ثابت
        canvas.height = 800 / aspectRatio; // تعديل الارتفاع للحفاظ على نسبة العرض إلى الارتفاع
        
        const ctx = canvas.getContext('2d');
        if (!ctx) return;
        
        // رسم الصورة
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        
        // إضافة النص
        const text = `كل عام وأنت بخير يا ${recipientName}`;
        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';
        ctx.font = 'bold 36px Tajawal';
        
        // إضافة ظل للنص
        ctx.shadowColor = 'rgba(0, 0, 0, 0.7)';
        ctx.shadowBlur = 8;
        ctx.shadowOffsetX = 2;
        ctx.shadowOffsetY = 2;
        
        // رسم النص
        ctx.fillStyle = 'white';
        ctx.fillText(text, canvas.width / 2, canvas.height - 80);
        
        // تحويل الكانفاس إلى URL للصورة
        const dataUrl = canvas.toDataURL('image/jpeg', 0.9);
        setGeneratedImage(dataUrl);
        setShareImage(dataUrl);
        
        // تعيين رابط المشاركة ليكون عنوان الموقع الحالي
        // هذا للمشاركة عبر واتساب وغيرها
        setShareUrl(window.location.href);
      };
      
      // تحميل الصورة
      // استخدام رابط نسبي بدءًا من location.origin
      img.src = window.location.origin + selectedCard.src;
    };
    
    generateImage();
  }, [recipientName, selectedCard]);
  
  // تعيين عنوان المشاركة
  useEffect(() => {
    setShareTitle(`تهنئة عيد سعيد لك يا ${recipientName}! 🎉`);
  }, [recipientName]);

  // التحقق من دعم المتصفح لـ Web Share API
  const canUseWebShare = () => {
    return typeof navigator !== 'undefined' && 
           'share' in navigator && 
           'canShare' in navigator && 
           generatedImage !== null;
  };

  // مشاركة الصورة والنص باستخدام Web Share API للمتصفحات الحديثة
  const handleWebShare = async () => {
    try {
      // تحويل الصورة من data URL إلى Blob
      if (!generatedImage) return;
      
      const response = await fetch(generatedImage);
      const blob = await response.blob();
      
      // إنشاء ملف من البلوب
      const file = new File([blob], `eid_card_${recipientName}.jpg`, { type: 'image/jpeg' });
      
      // التحقق من إمكانية المشاركة
      if ('canShare' in navigator && navigator.canShare({ files: [file] })) {
        await navigator.share({
          title: shareTitle,
          text: `${shareTitle}\n\n`,
          files: [file],
          url: shareUrl
        });
      } else {
        // إذا لم يكن بالإمكان مشاركة الملفات، نستخدم طريقة المشاركة البديلة
        await navigator.share({
          title: shareTitle,
          text: `${shareTitle}\n\n`,
          url: shareUrl
        });
      }
    } catch (error) {
      console.error('Error sharing:', error);
      // في حالة فشل المشاركة، نعرض أزرار المشاركة التقليدية
      setShowShareButtons(true);
    }
  };

  return (
    <PreviewContainer>
      <h2>بطاقة التهنئة جاهزة!</h2>
      
      {/* الكانفاس المخفي لإنشاء الصورة */}
      <HiddenCanvas ref={canvasRef} />
      
      <CardContainer ref={cardRef}>
        {/* عرض الصورة الناتجة بدلاً من الصورة الأصلية */}
        {generatedImage ? (
          <StyledImage src={generatedImage} alt={selectedCard.alt} />
        ) : (
          <>
            <StyledImage src={selectedCard.src} alt={selectedCard.alt} />
            <NameOverlay>{`كل عام وأنت بخير يا ${recipientName}`}</NameOverlay>
          </>
        )}
      </CardContainer>
      
      <h3>شارك البطاقة</h3>

      {/* زر المشاركة المتقدم الذي يستخدم Web Share API أولاً */}
      {canUseWebShare() && !showShareButtons && (
        <CustomShareButton onClick={handleWebShare}>
          <ShareIcon>🔗</ShareIcon>
          مشاركة البطاقة
        </CustomShareButton>
      )}
      
      {/* إظهار أزرار المشاركة التقليدية إذا لم يكن هناك دعم لـ Web Share API أو إذا فشلت المشاركة */}
      {(!canUseWebShare() || showShareButtons) && (
        <ShareContainer>
          <ShareButton>
            <WhatsappShareButton url={shareUrl} title={`${shareTitle}\n\n`}>
              <div style={{ background: '#25D366', padding: '10px', borderRadius: '50%', width: '40px', height: '40px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <span style={{ color: 'white', fontSize: '18px' }}>W</span>
              </div>
            </WhatsappShareButton>
          </ShareButton>
          
          <ShareButton>
            <FacebookShareButton url={shareUrl} hashtag="#EidMubarak">
              <div style={{ background: '#3b5998', padding: '10px', borderRadius: '50%', width: '40px', height: '40px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <span style={{ color: 'white', fontSize: '18px' }}>F</span>
              </div>
            </FacebookShareButton>
          </ShareButton>
          
          <ShareButton>
            <TwitterShareButton url={shareUrl} title={shareTitle}>
              <div style={{ background: '#1DA1F2', padding: '10px', borderRadius: '50%', width: '40px', height: '40px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <span style={{ color: 'white', fontSize: '18px' }}>X</span>
              </div>
            </TwitterShareButton>
          </ShareButton>
          
          <ShareButton>
            <TelegramShareButton url={shareUrl} title={shareTitle}>
              <div style={{ background: '#0088cc', padding: '10px', borderRadius: '50%', width: '40px', height: '40px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <span style={{ color: 'white', fontSize: '18px' }}>T</span>
              </div>
            </TelegramShareButton>
          </ShareButton>
        </ShareContainer>
      )}
      
      {/* إضافة زر تنزيل مباشر للصورة */}
      <Button 
        onClick={() => {
          if (generatedImage) {
            const a = document.createElement('a');
            a.href = generatedImage;
            a.download = `eid_card_${recipientName}.jpg`;
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
          }
        }}
        style={{ marginRight: '10px' }}
      >
        تنزيل البطاقة
      </Button>
      
      <Button onClick={onReset}>إنشاء بطاقة جديدة</Button>
    </PreviewContainer>
  );
};

export default CardPreview;