import React, { useState } from 'react';
import styled from 'styled-components';
import { cardTemplates } from './assets/cardData';
import CardSelector from './components/CardSelector';
import RecipientForm from './components/RecipientForm';
import CardPreview from './components/CardPreview';
import { CardTemplate } from './types';

const AppContainer = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  direction: rtl;
`;

const Header = styled.header`
  text-align: center;
  margin-bottom: 40px;
`;

const Title = styled.h1`
  color: #4caf50;
  font-size: 36px;
  margin-bottom: 10px;
`;

const Subtitle = styled.p`
  font-size: 18px;
  color: #666;
`;

function App() {
  const [recipientName, setRecipientName] = useState('');
  const [selectedCard, setSelectedCard] = useState<CardTemplate | null>(null);
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);

  const handleSelectCard = (card: CardTemplate) => {
    setSelectedCard(card);
  };

  const handleNameChange = (name: string) => {
    setRecipientName(name);
  };

  const handleSubmit = () => {
    setIsFormSubmitted(true);
  };

  const handleReset = () => {
    setRecipientName('');
    setSelectedCard(null);
    setIsFormSubmitted(false);
  };

  const isFormValid = recipientName.trim().length > 0 && selectedCard !== null;

  return (
    <AppContainer>
      <Header>
        <Title>بطاقات تهنئة العيد</Title>
        <Subtitle>أنشئ بطاقة تهنئة مخصصة وشاركها مع أحبائك</Subtitle>
      </Header>

      {!isFormSubmitted ? (
        <>
          <CardSelector
            cards={cardTemplates}
            selectedCard={selectedCard}
            onSelectCard={handleSelectCard}
          />
          <RecipientForm
            recipientName={recipientName}
            onNameChange={handleNameChange}
            onSubmit={handleSubmit}
            isFormValid={isFormValid}
          />
        </>
      ) : (
        selectedCard && (
          <CardPreview
            recipientName={recipientName}
            selectedCard={selectedCard}
            onReset={handleReset}
          />
        )
      )}
    </AppContainer>
  );
}

export default App;
